.menu {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 100%;
  background-color: #3f5252;
  opacity: 0;
  pointer-events: none;

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__nav {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    margin-bottom: 20px;
  }

  &__close {
    position: absolute;
    right: 0;

    @include on-tablet {
      right: 72px;
    }
  }

  &__language {
    margin-right: 60px;

    @include on-tablet {
      margin: 0;
    }
  }

  &__logo-icon {
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }
  }

  &__close-icon {
    transition: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition: 0.3s;
    }
  }

  &__language-icon {
    transition: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition: 0.3s;
    }
  }

  &__adress-point {
    transition: 0.3s;

    &:hover {
      border-bottom: 1px solid #f1f5f4;
      transition: 0.3s;
    }
  }

  &__link {
    transition: 0.3s;

    &:hover {
      border-bottom: 1px solid #f1f5f4;
      border: opacity (0.3);
      transition: 0.3s;
    }
  }

  &__wrapper {
    @include on-desktop {
      @include page-grid;
    }
  }

  &__content {
    @include on-desktop {
      grid-column: 1 /7;
    }
  }

  &__shedule {
    margin-bottom: 24px;

    @include on-tablet {
      display: inline-block;
      margin-right: 130px;
    }

    @include on-desktop {
      margin-right: 80px;
    }
  }

  &__shedule-day, &__adress-title {
    display: block;
    font-size: 14px;
    font-family: Raleway, sans-serif;
    line-height: 19.6px;
    font-weight: 400;
    color: #f1f5f4;
    text-align: left;
  }

  &__shedule-time {
    font-family: Raleway, sans-serif;
    font-weight: 600;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #f1f5f4;
  }

  &__adress {
    margin-bottom: 40px;

    @include on-tablet {
      display: inline-block;
    }
  }

  &__adress-point {
    text-decoration: none;
    font-family: Raleway, sans-serif;
    font-weight: 600;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #f1f5f4;
  }

  &__item {
    margin-bottom: 24px;

    @include on-desktop {
      margin-bottom: 64px;
    }
  }

  &__links {
    list-style: none;
    padding: 0;

    @include on-desktop {
      margin-top: 30px;
    }
  }

  &__link {
    text-decoration: none;
    font-size: 20px;
    font-family: Playfair Display;
    line-height: 30px;
    font-weight: 700;
    color: #f1f5f4;
    text-align: left;

    @include on-tablet {
      font-size: 48px;
      line-height: 58px;
    }

    @include on-desktop {
      letter-spacing: 1.9px;
    }
  }

  &__button-link {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 16px;
    font-weight: 600;
    color: #f1f5f4;
    text-decoration: none;
    text-align: center;
  }

  &__bottom-line {
    width: 100%;
    height: 1px;
    margin: 40px 0;
    background-color: #f1f5f4;

    @include on-tablet {
      margin: 48px 0;
    }

    @include on-desktop {
      margin-bottom: 58px;
      opacity: 0.3;
    }
  }

  &__button {
    @include on-tablet {
      margin-bottom: 34px;
    }
  }

  &__photo {
    @include on-desktop {
      grid-column: 7 /-1;
      margin-right: -55px;
    }
  }

  &__foto-tab {
    display: none;

    @include on-tablet {
      display: block;
      margin: 0 auto;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-ds {
    display: none;

    @include on-desktop {
      display: block;
      width: 100%;
    }
  }
}
