html {
  scroll-behavior: smooth;
}

.page {
  margin: 0;
  scroll-behavior: smooth;
}

.container {
  @include content-padding-inline;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  border: none;
  box-sizing: border-box;
  background-color: #cd4d31;
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    background-color: #af3419;
    transition: 0.3s;
  }

  &:focus {
    background-color: #ea340d;
  }

  @include on-tablet {
    width: 330px;
  }

  @include on-desktop {
    width: 270px;
  }
}

.button-2 {
  display: block; display: block;
  text-align: center;
  padding-top: 18px;
  color: #cd4d31;
  text-decoration: none;
  font-size: 16px;
  font-family: Raleway, sans-serif;
  line-height: 16px;
  font-weight: 600;
  width: 100%;
  height: 60px;
  border: 1px solid #cd4d31;
  box-sizing: border-box;
  cursor: pointer;
  margin-top: 30px;
  transition: 0.3s;

  &:hover {
    border-color: #af3419;
    transition: 0.3s;
  }

  &::after {
    content: '';
    background-image: url(../images/icons/arrow.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 20px;
    height: 16px;
    margin-left: 8px;
  }
}
