.news {
  margin-bottom: 100px;

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__title {
    margin-bottom: 50px;
    font-size: 28px;
    font-family: Playfair Display;
    line-height: 35px;
    font-weight: 700;
    color: #1c1b29;
    text-align: left;

    @include on-tablet {
      grid-column: 1 /5;
      margin: 20px 0 60px;
      font-size: 40px;
      line-height: 50px;
    }

    @include on-desktop {
      grid-column: 1 /4;
      font-size: 48px;
      line-height: 58px;
    }
  }

  &__card-1 {
    margin-bottom: 50px;

    @include on-tablet {
      grid-column: 1 /4;
      margin: 0;
    }

    @include on-desktop {
      grid-column: 1 /7;
    }
  }

  &__card-2 {
    @include on-tablet {
      grid-column: 4 /-1;
    }

    @include on-desktop {
      grid-column: 7 /-1;
    }
  }

  &__photo {
    margin-bottom: 18px;

    @include on-tablet {
      margin-bottom: 24px;
    }
  }

  &__foto {
    width: 100%;
  }

  &__foto-1-mb {
    @include on-tablet {
      display: none;
    }
  }

  &__foto-1-tab {
    display: none;

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-1-ds {
    display: none;

    @include on-desktop {
      display: block;
    }
  }

  &__foto-2-mb {
    @include on-tablet {
      display: none;
    }
  }

  &__foto-2-tab {
    display: none;

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-2-ds {
    display: none;

    @include on-desktop {
      display: block;
    }
  }

  &__date {
    font-size: 14px;
    font-family: Raleway, sans-serif;
    line-height: 19.6px;
    font-weight: 400;
    color: #1c1b29;
    text-align: left;
  }

  &__card-title {
    margin: 0 0 20px;
    font-size: 20px;
    font-family: Playfair Display;
    line-height: 25px;
    font-weight: 700;
    color: #1c1b29;
    text-align: left;

    @include on-tablet {
      line-height: 30px;
    }
  }

  &__card-text {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 24px;
    font-weight: 400;
    color: #504f5e;
    text-align: left;
  }

  &__button {
    @include on-tablet {
      grid-column: 5 /-1;
      grid-row: 1;
      margin-top: 20px;
    }

    @include on-desktop {
      grid-column: 10 /-1;
    }
  }
}
