.header {
  margin-bottom: 100px;

  &__wrapper {
    background-color: #3f5252;
  }

  &__nav {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
  }

  &__nav-logo {
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }
  }

  &__nav-menu {
    transition: 0.3s;

    @include on-tablet {
      margin-right: 72px;
    }

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }
  }

  &__nav-country {
    display: none;

    @include on-tablet {
      display: block;
      position: absolute;
      right: 0;
      transition: 0.3s;
    }

    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
    }
  }

  &__content-wrapper {
    position: relative;

    @include on-tablet {
      @include page-grid;
    }
  }

  &__content {
    margin-top: 50px;

    @include on-tablet {
      grid-column: 1 /6;
      margin-top: 100px;
    }

    @include on-desktop {
      grid-column: 1 /7;
    }
  }

  &__dates {
    font-family: Raleway, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
    text-align: left;
    color: #fff;
  }

  &__title {
    font-family: Playfair, Display;
    font-size: 48px;
    font-weight: 700;
    line-height: 48px;
    text-align: left;
    margin: 15px 0 25px;
    color: #f1f5f4;

    @include on-tablet {
      font-size: 72px;
      line-height: 72px;
      letter-spacing: 1.8px;
    }

    @include on-desktop {
      font-size: 96px;
      line-height: 96px;
    }
  }

  &__description {
    margin-bottom: 40px;
    width: 80%;
    font-family: Raleway, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    color: #f1f5f4;

    @include on-tablet {
      width: 54%;
      letter-spacing: 0.3px;
    }

    @include on-desktop {
      width: 100%;
      margin-bottom: 70px;
    }
  }

  &__button-link {
    font-family: Raleway, sans-serif;
    font-size: 16px;
    font-weight: 600;
    line-height: 16px;
    text-align: center;
    text-decoration: none;
    color: #f1f5f4;
  }

  &__photo {
    margin-top: 50px;
    text-align: center;

    @include on-tablet {
      grid-column: 2 /6;
      margin-top: 70px;
    }

    @include on-desktop {
      grid-column: 7 /-1;
      margin-right: -55px;
      margin-top: 50px;
    }
  }

  &__foto--mb {
    margin-bottom: -4px;

    @include on-tablet {
      display: none;
    }
  }

  &__foto--tab {
    display: none;

    @include on-tablet {
      display: block;
      margin: 0 auto;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto--ds {
    display: none;

    @include on-desktop {
      display: block;
      width: 100%;
    }
  }

  &__time {
    display: none;

    @include on-desktop {
      display: block;
      position: absolute;
      bottom: 0;
      font-family: Playfair Display;
      font-size: 14px;
      font-weight: 700;
      line-height: 17.5px;
      text-align: left;
      color: #f1f5f4;
      opacity: 0.7;
    }
  }

  &__bottom-line {
    display: none;

    @include on-desktop {
      display: block;
      position: absolute;
      bottom: 40px;
      left: 100px;
      width: 470px;
      height: 1px;
      background-color: #f1f5f4;
      opacity: 0.3;
    }
  }
}
