.footer {
  padding: 36px 0;
  background-color: #3f5252;

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__contacts {
    @include on-tablet {
      grid-column: 1 /3;
      margin-top: 30px;
    }

    @include on-desktop {
      grid-column: 1 /5;
    }
  }

  &__contacts-title {
    font-family: Playfair Display;
    font-size: 18px;
    font-weight: 700;
    line-height: 27px;
    margin: 0;
    text-align: left;
    color: #f1f5f4;

    @include on-tablet {
      font-size: 20px;
      line-height: 30px;
    }
  }

  &__contacts-links {
    list-style: none;
    padding: 0;
    margin-bottom: 26px;

    @include on-tablet {
      margin-bottom: 46px;
    }
  }

  &__contacts-link {
    font-family: Raleway, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    color: #f1f5f4;
    text-decoration: none;
    transition: 0.3s;

    &:hover {
      border-bottom: 1px solid #f1f5f4;
      transition: 0.3s;
    }
  }

  &__icon-link {
    text-decoration: none;
  }

  &__icon {
    padding-right: 15px;
    transition: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition: 0.3s;
    }
  }

  &__shedule {
    @include on-tablet {
      grid-column: 3 /5;
    }

    @include on-desktop {
      grid-column: 5 /9;
    }
  }

  &__shedule-title {
    margin: 40px 0 14px;
    font-family: Playfair Display;
    font-weight: 700;
    font-size: 18px;
    text-align: left;
    line-height: 27px;
    color: #f1f5f4;

    @include on-tablet {
      font-size: 20px;
      line-height: 30px;
      margin-top: 30px;
    }
  }

  &__shedule-time {
    font-size: 16px;
    font-family: Raleway,sans-serif;
    line-height: 24px;
    font-weight: 400;
    color: #f1f5f4;
    text-align: left;
  }

  &__menu {
    @include on-tablet {
      grid-column: 5 /-1;
    }

    @include on-desktop {
      grid-column: 9 /-1;
    }
  }

  &__menu-title {
    margin: 40px 0 14px;
    font-size: 20px;
    font-family: Playfair Display;
    line-height: 30px;
    font-weight: 700;
    color: #f1f5f4;
    text-align: left;

    @include on-tablet {
      margin-top: 30px;
    }
  }

  &__menu-links {
    list-style: none;
    padding: 0;
  }

  &__menu-link {
    text-decoration: none;
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 24px;
    font-weight: 400;
    color: #f1f5f4;
    text-align: left;
    transition: 0.3s;

    &:hover {
      border-bottom: 1px solid #f1f5f4;
      transition: 0.3s;
    }
  }

  &__bottom {
    position: relative;
    margin-top: 60px;

    @include on-tablet {
      grid-column: 1 /-1;
    }
  }

  &__years,&__terms {
    font-size: 14px;
    font-family: Raleway, sans-serif;
    line-height: 19.6px;
    font-weight: 400;
    color: #f1f5f4;
    text-align: center;
    padding-bottom: 5px;
  }

  &__years {
    padding-right: 30px;

    @include on-desktop {
      padding-right: 100px;
    }
  }

  &__arrow {
    position: absolute;
    right: 0;
    bottom: -5px;
  }

  &__arrow-up {
    transition: 0.3s;

    &:hover {
      transform: scale(1.2);
      transition: 0.3s;
    }
  }
}
