.visit-us {
  margin-bottom: 100px;
  background-color: #3f5252;

  &__content {
    padding-bottom: 48px;

    @include on-tablet {
      @include page-grid;
      padding-bottom: 68px;
    }

    @include on-desktop {
      padding: 0;
    }
  }

  &__photo {
    @include on-desktop {
      grid-column: 1 /6;
      margin-left: -55px;
    }
  }

  &__foto {
    display: none;

    @include on-desktop {
      display: block;
    }
  }

  &__title {
    margin: 0;
    padding-top: 50px;
    font-size: 38px;
    font-family: Playfair Display;
    line-height: 38px;
    font-weight: 700;
    letter-spacing: 1.2px;
    color: #f1f5f4;
    text-align: left;

    @include on-tablet {
      grid-column: 1 /6;
      width: 80%;
      padding-top: 74px;
      font-size: 62px;
      line-height: 62px;
    }

    @include on-desktop {
      grid-column: 6 /-1;
      grid-row: 1;
      font-size: 80px;
      line-height: 80px;
      width: 90%;
      margin-top: 155px;
    }
  }

  &__description {
    margin: 32px 0;
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 24px;
    font-weight: 400;
    letter-spacing: 0.3px;
    color: #f1f5f4;
    text-align: left;

    @include on-tablet {
      grid-column: 1 /5;
      margin-bottom: 50px;
    }

    @include on-desktop {
      grid-column: 6 /11;
      grid-row: 1;
      margin-top: 420px;
      font-size: 15px;
    }
  }

  &__button {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 16px;
    font-weight: 600;
    color: #f1f5f4;
    text-align: center;

    @include on-tablet {
      grid-column: 1 /4;
    }

    @include on-desktop {
      grid-column: 6 /9;
      grid-row: 1;
      margin-top: 530px;
    }
  }
}
