.subscribe {
  margin-bottom: 100px;

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__title {
    font-size: 28px;
    font-family: Playfair Display;
    line-height: 35px;
    font-weight: 700;
    color: #1c1b29;
    text-align: left;

    @include on-tablet {
      grid-column: 1 /-1;
      font-size: 40px;
      line-height: 50px;
      letter-spacing: 1.55px;
      margin: 0;
    }

    @include on-desktop {
      grid-column: 1 /7;
      font-size: 48px;
      line-height: 58px;
      margin: 12px 0 38px;
    }
  }

  &__text {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 24px;
    font-weight: 400;
    color: #1c1b29;
    text-align: left;
    margin-bottom: 38px;

    @include on-tablet {
      grid-column: 1 /6;
      margin-bottom: 48px;
    }

    @include on-desktop{
      grid-column: 7 /-1;
    }
  }

  &__inputs {
    @include on-tablet {
      grid-column: 1 /5;
    }

    @include on-desktop {
      grid-column: 1 /6;
    }
  }

  &__input {
    border: none;
    border-bottom: 1px solid #1c1b29;
    width: 100%;
    height: 30px;
    background-color: transparent;
    outline: none;
    margin-bottom: 30px;
  }

  &__button {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 16px;
    font-weight: 600;
    color: #f1f5f4;
    text-align: center;
  }
}
