.events {
  margin-bottom: 80px;

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__title {
    font-size: 28px;
    font-family: Playfair Display;
    line-height: 35px;
    font-weight: 700;
    color: #1c1b29;
    text-align: left;

    @include on-tablet {
      grid-column: 1 /4;
      margin-top: 30px;
      font-size: 40px;
      line-height: 50px;
      letter-spacing: 2px;
    }

    @include on-desktop {
      grid-column: 1 /6;
      margin-top: 24px;
      font-size: 48px;
      line-height: 58px;
    }
  }

  &__photo {
    margin: 50px 0 25px;

    @include on-tablet {
      grid-column: 1 /4;
      margin: 50px 0 0;
    }

    @include on-desktop {
      grid-column: 1 /5;
    }
  }

  &__foto {
    width: 100%;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.02);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__foto-1-mb {
    @include on-tablet {
      display: none;
    }
  }

  &__foto-1-tab {
    display: none;

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-1-ds {
    display: none;

    @include on-desktop {
      display: block;
    }
  }

  &__foto-2-mb {
    @include on-tablet {
      display: none;
    }
  }

  &__foto-2-tab {
    display: none;

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-2-ds {
    display: none;

    @include on-desktop {
      display: block;
    }
  }

  &__card-1 {
    @include on-tablet {
      grid-column: 4 /-1;
      margin-top: 52px;
    }

    @include on-desktop {
      grid-column: 5 /11;
    }
  }

  &__card-2 {
    @include on-tablet {
      grid-column: 4 /-1;
      margin-top: 52px;
    }

    @include on-desktop {
      grid-column: 5 /11;
    }
  }

  &__card-title {
    font-size: 20px;
    font-family: Playfair Display;
    line-height: 25px;
    font-weight: 700;
    color: #1c1b29;
    text-align: left;
    margin-top: 20px;

    @include on-tablet {
      font-size: 24px;
      line-height: 30px;
    }
  }

  &__card-text {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 24px;
    font-weight: 400;
    color: #504f5e;
    text-align: left;
    margin-bottom: 30px;

    @include on-tablet {
      margin-bottom: 40px;
    }
  }

  &__button {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 16px;
    font-weight: 600;
    color: #f1f5f4;
    text-align: center;
    text-decoration: none;
  }

  &__button-2 {
    @include on-tablet {
      grid-column: 5 /-1;
      grid-row: 1;
    }

    @include on-desktop {
      grid-column: 10 /-1;
    }
  }
}
