.exhibitions {
  margin-bottom: 100px;

  @include on-tablet {
    margin-bottom: 140px;
  }

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__title {
    font-size: 28px;
    font-family: Playfair Display;
    line-height: 35px;
    font-weight: 700;
    color: #1c1b29;
    text-align: left;

    @include on-tablet {
      font-size: 40px;
      line-height: 50px;
      letter-spacing: 1.1px;
      grid-column: 1 /5;
      margin: 30px 0 50px;
    }

    @include on-desktop {
      grid-column: 1 /7;
      font-size: 48px;
      line-height: 58px;
    }
  }

  &__card-1 {
    margin-bottom: 50px;

    @include on-tablet {
      grid-column: 1 /4;
      margin: 0;
    }

    @include on-desktop {
      grid-column: 1 /7;
    }
  }

  &__card-2 {
    @include on-tablet {
      grid-column: 4 /-1;
    }

    @include on-desktop {
      grid-column: 7 /-1;
    }
  }

  &__photo {
    margin-bottom: 24px;
  }

  &__foto {
    transition: 0.3s;
    cursor: pointer;

    &:hover {
      transform: scale(1.02);
      box-shadow: 5px 5px 5px #ada6a6;
      transition: 0.3s;
    }
  }

  &__foto-mb {
    width: 100%;

    @include on-tablet {
      display: none;
    }
  }

  &__foto-tab {
    display: none;

    @include on-tablet {
      display: block;
      width: 100%;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-ds {
    display: none;

    @include on-desktop {
      display: block;
      width: 100%;
    }
  }

  &__shedule {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 24px;
    font-weight: 400;
    color: #cd4d31;
    text-align: left;
  }

  &__card-title {
    font-size: 20px;
    font-family: Playfair Display;
    line-height: 25px;
    font-weight: 700;
    color: #1c1b29;
    text-align: left;
    margin: 20px 0;

    @include on-tablet {
      font-size: 24px;
      line-height: 30px;
      width: 340px;
    }

    @include on-desktop {
      width: 100%;
    }
  }

  &__card-title-2 {
    @include on-tablet {
      margin-bottom: 50px;
    }

    @include on-desktop {
      margin: 20px 0;
    }
  }

  &__card-description {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 24px;
    font-weight: 400;
    color: #504f5e;
    text-align: left;
    margin-bottom: 30px;

    @include on-tablet {
      width: 340px;
    }

    @include on-desktop {
      width: 100%;
    }
  }

  &__button {
    width: 100%;

    @include on-desktop {
      width: 48%;
    }
  }

  &__button-2 {
    @include on-tablet {
      grid-column: 5 /-1;
      grid-row: 1;
      margin-bottom: 12px;
    }

    @include on-desktop {
      grid-column: 10 /-1;
    }
  }

  &__button-2-link {
    font-size: 16px;
    font-family: Raleway, sans-serif;
    line-height: 16px;
    font-weight: 600;
    text-decoration: none;
    color: #cd4d31;
    text-align: center;
    transition: 0.3s;
    padding: 10% 12%;

    &::after {
      content: '';
      background-image: url(../images/icons/arrow.svg);
      background-repeat: no-repeat;
      display: inline-block;
      width: 20px;
      height: 16px;
      margin-left: 8px;
    }

    &:hover {
      color: #af3419;
      transition: 0.3s;
    }
  }
}
