.button {
  box-sizing: border-box;
  cursor: pointer;
  background-color: #cd4d31;
  border: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  transition: all .3s;
  display: flex;
}

.button:hover {
  background-color: #af3419;
  transition: all .3s;
}

.button:focus {
  background-color: #ea340d;
}

@media (width >= 768px) {
  .button {
    width: 330px;
  }
}

@media (width >= 1280px) {
  .button {
    width: 270px;
  }
}

.button-2 {
  text-align: center;
  color: #cd4d31;
  box-sizing: border-box;
  cursor: pointer;
  border: 1px solid #cd4d31;
  width: 100%;
  height: 60px;
  margin-top: 30px;
  padding-top: 18px;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.button-2:hover {
  border-color: #af3419;
  transition: all .3s;
}

.button-2:after {
  content: "";
  background-image: url("arrow.8951bb24.svg");
  background-repeat: no-repeat;
  width: 20px;
  height: 16px;
  margin-left: 8px;
  display: inline-block;
}

